.loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #6B63FF; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}