.tabs {
  display: flex;
  width: 100%;
  justify-content: space-between;
  border: 1px solid #6B63FF;
  border-radius: 5px;
}
.tabs .tab {
  cursor: pointer;
  width: 33.33%;
  text-align: center;
  color: #6B63FF;
}

.active {
  background-color: #6B63FF;
  color: #fff!important;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
}

.ant-layout-header {
  color: #fff !important;
}

.ant-btn {
  line-height: 1.5715;
  position: relative;
  display: inline-block;
  font-weight: 400;
  white-space: nowrap;
  text-align: center;
  background-image: none;
  border: 1px solid transparent;
  box-shadow: 0 2px 0 rgb(0 0 0 / 2%);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  height: 60px!important;
  padding: 4px 15px;
  font-size: 24px!important;
  border-radius: 2px;
  margin-right: 10px;
}

.link-button {
  border: 0px!important;
  text-align: left!important;
  height: 32px!important;
  font-size: 24px!important;
  box-shadow: none!important;
  color: #6B63FF!important;
  text-decoration: underline;
  cursor: pointer;
}

.m-t-50-px {
  margin-top: 50px;
}

.ant-layout-content {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  line-height: 50px;
  background: #fff;
  max-height: 90vh;
}

.ant-layout-footer {
  position: absolute;
  width: 100vw;
  bottom: 0;
}
.question-list {
  max-width: 100vw;
  max-height: 90vh;
}
.overflow-hidden {
  overflow: hidden;
}

.questions {
  width: 50%;
}

.questions div button {
  width: 40%;
}

.question {
  width: 100%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  margin-top: 10%;
}
.question p {
  font-size: 36px;
  margin-bottom: 0.2em;
  line-height: 1.5;
}
.ant-button {
  background-color: #001529;
}
h2 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

small {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

