/* Language: css */
/* sticky header */
.sticky-header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1;
}

.sticky-header + .content {
  margin-top: 50px;
}

.sticky {
  position: fixed;
  top: 100px;
}

.float-right {
  float: right;
}

.consent-container {
  margin-left: auto;
  margin-right: 10%;
}
/* display: flex; */
.flex {
  display: flex;
}

/* container */
.container {
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.back-container { 
  margin-left: 10%;
  margin-right: 10%;
}

/* space-between */
.justify-space-between {
  display: flex;
  justify-content: space-between;
}

b {
  display: block;
}

.checked-list {
  list-style: none;
}

.checked-list li:before {
  content: "âœ“";
  width: 20px;
  height: 20px;
  margin-right: 5px;
  color: #fff;
  background-color: #6B63FF;
  border-radius: 20%;
}

.left {
  flex-basis: 50%;
}

.right {
  flex-basis: 50%;
}

.wrap {
  flex-wrap: wrap;
}

.column {
  flex-direction: column;
}

.align-center {
  align-items: center;
  text-align: center;
  justify-content: center;
}
.section {
  margin-top: 50px;
  margin-bottom: 10px;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.vh-90 {
  height: 90vh;
}

.interview {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.scroll-table {
  overflow-y: scroll;
  scrollbar-color: #000;
}

::-webkit-scrollbar-thumb {
  width: 10px;
  color: #000;
}

.allergies {
  line-height: 30px !important;
  align-items: flex-start;
  width: 50%;
  text-align: left;
  margin-top: 10%;
}
.allergies h2,
small {
  margin: 0;
  padding: 0;
}

.allergies .action {
  margin-top: 10px;
}
.chips {
  display: flex;
  width: 100%;
  height: auto;
  flex-wrap: wrap;
}
.chip {
  display: flex;
  margin: 5px 5px 0 0;
  border: 1px solid gray;
  border-radius: 20px;
  justify-content: space-between;
  padding: 2px 2px 2px 4px;
  background: #6b63ff;
  color: #fff;
  white-space: nowrap;
  width: auto;
}

.ant-input {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: 'tnum', "tnum";
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 24px!important;
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  transition: all 0.3s;
}

.chip .allergy {
  margin-right: 5px;
}

.chip .close-button {
  border: 1px solid gray;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  text-align: center;
  padding: 0;
  background: #fff;
  color: #6b63ff;
  cursor: pointer;
}

.vh-50 {
  width: 50%;
}

.error-content {
  width: 50%;
  line-height: 30px;
}

.error-content p {
  padding: 0;
  margin: 0;
}

.error {
  color: #ff0000;
  padding: 0;
  margin: 0;
  font-size: 40px;
}

.error-message {
  color: #ff0000;
  line-height: 20px;
  font-size: medium;
}

form {
  line-height: 30px;
}

.message-fl {
  color: #ff000090;
  margin-top: 10px;
  font-size: 16px;
}

.message-fl::before {
  content: "* ";
}

.logo-app {
  color: #fff;
}

.logo-app:hover {
  color: #fff;
}
.responsive {
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-size: 36px;
}

.sub-title {
  font-size: 20px;
}

.search {
  line-height: 15px;
}

.helper-message {
  font-size: 16px !important;
  line-height: 16px;
  color: rgb(73, 77, 85);
  font-weight: 300;
  margin-bottom: 20px !important;
}

.m-t-10-px {
  margin-top: 10px!important;
}

.m-b-10-px {
  margin-bottom: 10px!important;
}

.m-b-0-px {
  margin-bottom: 0px!important;
}

.description-message {
  display: block;
  color:rgb(73, 77, 85) !important;
  line-height: 2;
}

.consent {
  margin-top: 0;
}

.m-t-10-p {
  margin-top: 10%;
}

.street {
  width: 40%;
}

.places {
  margin-right: 20px;
}

.active-pharmacy {
  background-color: #6B63FF;
  color: #fff!important;
  padding: 5px;
  border-radius: 5px;
}

.active-pharmacy div div h4 {
  color: #fff!important;
  padding-left: 5px;
}

.active-pharmacy div div div {
  color: #fff!important;
  padding-left: 5px;
}

.list-item {
  line-height: 1;
}
.upload-button {
  margin-bottom: 2px;
}
.upload-button div span .ant-button{
  min-width: 300px;
}

.details {
  margin: 0;
  padding: 0;
  line-height: 2;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 550px) and (-webkit-min-device-pixel-ratio: 2){
  .responsive {
    height: 100%;
    max-width: 90%;
  }
  .interview {
    display: none;
  }
  .questions, .allergies, .consent {
    width: 100%;
  }
  .question p {
    font-size: 28px;
    margin-bottom: 0.2em;
    text-align: justify;
  }
  .ant-checkbox-wrapper {
    line-height: inherit;
  }
  .left {
    flex-basis: 100%;
  }

  .image {
    display: none;
  }

  .right {
    flex-basis: 100%;
    margin-top: 1rem;
  }

  .column-on-mobile {
    flex-direction: column;
  }
  .tabs {
    width: 100%;
  }
  .tab {
    font-size: x-small;
  }
  .justify-space-between {
    flex-direction: column;
  }
  .street {
    width: 100%;
  }
  .hide-on-mobile {
    display: none;
  }
  .places {
    margin-right: 0;
  }
  .consent-container {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
}
