.radiobtn {
  position: relative;
  display: block;
  margin-bottom: 10px;
  border-radius: 10px;
}

.radiobtn label {
  display: block;
  /* background: #B8B0FF; */
  color: #444;
  border-radius: 5px;
  padding: 10px 20px;
  border: 2px solid #6B63FF32;
  margin-bottom: 5px;
  cursor: pointer;
}

.radiobtn label::before,
.radiobtn label::after {
  content: "";
  position: absolute;
  right: 11px;
  top: 11px;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  background: lighten(#6B63FF, 15%);
}

.radiobtn label::before {
  background: transparent;
  transition: 0.1s width cubic-bezier(0.075, 0.82, 0.165, 1) 0s,
    0.3s height cubic-bezier(0.075, 0.82, 0.165, 2) 0.1s;
  z-index: 2;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: 13px;
  background-position: center;
  width: 0;
  height: 0;
  background-image: url(data:image/svg+xml;base64,);
}

input[type="radio"] {
  display: none;
  position: absolute;
  width: 100%;
  appearance: none;
}

input[type="radio"] + label {
  font-size: 20px;
}

input[type="radio"]:checked + label {
  background: #B8B0FF;
  animation-name: blink;
  animation-duration: 1s;
  border-color: #6B63FF;
  color: #FFF;
}

input[type="radio"]:checked + label::before {
  width: 20px;
  height: 20px;
}

input[type="radio"]:checked + label::after {
  background: #6B63FF;
}

@keyframes blink {
	0% {
		background-color: lighten(#6B63FF, 15%);
	}
	10% {
		background-color: lighten(#6B63FF, 15%);
	}
	11% {
		background-color: lighten(#6B63FF, 20%);
	}
	29% {
		background-color: lighten(#6B63FF, 20%);
	}
	30% {
		background-color: lighten(#6B63FF, 15%);
	}
	50% {
		background-color: lighten(#6B63FF, 20%);
	}
	45% {
		background-color: lighten(#6B63FF, 15%);
	}
	50% {
		background-color: lighten(#6B63FF, 20%);
	}
	100% {
		background-color: lighten(#6B63FF, 15%);
	}
}